/* Карта новини */
.news-latest-card {
  background:#f6f6f9;
  border-radius:16px;
  box-shadow:0 4px 10px rgba(0,0,0,.05);
  display:flex; flex-direction:column; height:100%;
}
.card-image-wrapper{height:180px;}
.card-image{width:100%;height:100%;object-fit:cover;display:block;}
.card-gradient-bar{height:6px;background:linear-gradient(90deg,#9333ea,#3b82f6);}
.card-content{padding:16px;}
.card-title{font-size:18px;font-weight:700;margin-bottom:10px;color:#1e1e1e;}
.card-meta{font-size:14px;color:#4b5563;}
.card-type{font-weight:500;}
.card-date{font-style:italic;}

/* Контейнер Swiper: навігація зовні, слайди обрізаються всередині */
.swiper-container{position:relative;overflow:visible;}
.news-slider.swiper{overflow:hidden;border-radius:16px;}

/* Стрілки (відносно контейнера) */
.swiper-button-prev,
.swiper-button-next{
  position:absolute;top:50%;transform:translateY(-50%);
  z-index:10;width:40px;height:40px;border-radius:50%;
  background:#fff;color:#3b82f6;box-shadow:0 2px 6px rgba(0,0,0,.1);
  display:flex;align-items:center;justify-content:center;
}
.swiper-button-prev{left:-20px;}
.swiper-button-next{right:-20px;}
.swiper-button-prev::after,.swiper-button-next::after{font-size:16px;font-weight:700;}

/* Пагінація під блоком (у контейнері) */
.swiper-container .swiper-pagination{
  position:static!important;margin-top:16px;text-align:center;
}
.swiper-pagination-bullet{
  width:10px;height:10px;border-radius:50%;
  background:#d1d5db;opacity:1;margin:0 4px!important;
}
.swiper-pagination-bullet-active{background:#3b82f6;}
